@font-face {
  font-family: "Akkurat LL";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("./fonts/AkkuratLLCyr-Light.woff2") format("woff2"), url("./fonts/AkkuratLLCyr-Light.woff") format("woff");
}
@font-face {
  font-family: "Akkurat LL";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("./fonts/AkkuratLLCyr-Regular.woff2") format("woff2"), url("./fonts/AkkuratLLCyr-Regular.woff") format("woff");
}
*, *::before, *::after {
  box-sizing: inherit;
}

html {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100%;
  height: 100vh;
  margin: 0 auto;
  padding: 0;
  font-family: "Akkurat LL", "Arial", sans-serif, sans-serif;
  font-weight: normal;
}

img {
  max-width: 100%;
  width: 100%;
}

a {
  text-decoration: none;
}

.hidden {
  position: absolute;
  width: 0.1px !important;
  height: 0.1px !important;
  margin: 0 !important;
  padding: 0 !important;
  opacity: 0 !important;
  overflow: hidden !important;
  z-index: -1;
  transition: all 0.3s;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.header {
  background: linear-gradient(180deg, rgba(0, 49, 55, 0.81) 0%, #003137 100%);
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  min-width: 360px;
  max-width: 560px;
  height: 80px;
  margin: 0 auto;
  padding: 30px 16px;
}

.header__logo {
  min-width: 124px;
  min-height: 15px;
}

.header__toggle {
  width: 35px;
  height: 22px;
  padding: 0;
  background-color: transparent;
  border: none;
  cursor: pointer;
}

.lang {
  display: none;
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.lang__link {
  color: #ffffff;
}

.navigation {
  z-index: 15;
}

.navigation__list {
  display: flex;
  flex-direction: column;
  gap: 19px;
  margin: 0;
  padding: 0;
  list-style-type: none;
  background-color: #003137;
  min-width: 360px;
  max-width: 560px;
  padding-bottom: 179px;
}

.navigation__item {
  padding: 10px;
}

.navigation__link {
  display: inline-block;
  min-height: 35px;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 50px;
  line-height: 70px;
  color: #5D9F93;
  cursor: pointer;
}

.main__wrapper {
  margin: 0 auto;
  min-width: 360px;
  max-width: 560px;
}

.main__container {
  background-color: #003137;
}

.main__image {
  display: block;
}

.main__banner {
  position: relative;
}

.main__play-sing {
  position: absolute;
  top: calc(50% - 23px);
  left: calc(50% - 23px);
  cursor: pointer;
}

.about-us {
  position: relative;
  padding: 105px 4.4% 198px;
  background-color: #003137;
}

.about-us__title {
  position: relative;
  padding: 0 40px 40px 0;
  margin: 0;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 55px;
  line-height: 52px;
  color: #E1F1E3;
}

.about-us__content {
  position: relative;
  margin: 0;
  padding: 0 35px 0 0;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  color: #E1F1E3;
}
.about-us__content::before {
  content: "";
  position: absolute;
  right: 0px;
  width: min(180vw, 700px);
  height: min(180vw, 700px);
  background-image: url(../img/aqm_logo_full.svg);
  bottom: -50px;
  background-position: bottom right;
  background-size: 100% 100%;
  bottom: -30px;
}

.projects {
  padding: 53px 4.4% 90px;
}

.projects__title {
  margin: 0;
  padding: 0 0 12px;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 35px;
  line-height: 69px;
  color: #151919;
}

.projects__container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.projects__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 200px;
  height: 50px;
  padding: 12px 36px;
  background: #E0804E;
  border-radius: 64px;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #ffffff;
}

.card {
  padding-bottom: 46px;
}

.card__image {
  display: block;
  width: 100%;
  margin: 0 auto;
}

.card__text {
  padding: 5px 0 0;
  margin: 0;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 140%;
  color: #151919;
}

.work {
  background-color: #003137;
}

.work__container {
  position: relative;
  padding: 23px 4.4% 54px;
}
.work__container::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 360px;
  height: 265px;
  background-image: url(../img/stroke_mob_work.svg);
  background-repeat: no-repeat;
  background-position: top left;
  background-size: 344px 264px;
}

.work__slogan {
  margin: 0;
  padding: 0 0 70px;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 55px;
  line-height: 58px;
  color: #E1F1E3;
}

.work__text {
  margin: 0;
  padding: 0 0 28px;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #E1F1E3;
}

.work__title {
  margin: 0;
  padding: 0 0 7px;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 52px;
  color: #E1F1E3;
}

.work__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.work__item {
  padding: 20px 0;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  color: #E1F1E3;
  border-bottom: 1px solid #5D9F93;
}

.work__item:last-child {
  border-bottom: none;
}

.construction {
  background-color: #E1F1E3;
}

.construction__container {
  position: relative;
  padding: 12px 16px 50px;
}
.construction__container::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 360px;
  height: 440px;
  background-image: url(../img/stroke_mob_construction.svg);
  background-repeat: no-repeat;
  background-position: top left;
  background-size: 360px 440px;
}

.construction__slogan {
  margin: 0 0 43px;
  padding: 0;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 55px;
  line-height: 58px;
  color: #003137;
}

.construction__text {
  margin: 0 0 73px;
  padding: 0;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #003137;
}

.construction__title {
  margin: 0 0 22px;
  padding: 0;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 52px;
  color: #003137;
}

.construction__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.construction__item {
  padding: 17px 0;
  border-bottom: 1px solid #5D9F93;
}

.construction__item:last-child {
  border-bottom: none;
}

.biome {
  background-color: #003137;
}

.biome__container--up {
  position: relative;
  min-height: 470px;
  padding: 48px 16px 290px;
  z-index: 5;
}

.biome__container--down {
  min-height: 572px;
  padding: 135px 16px 195px;
}

.biome__title {
  margin: 0;
  padding: 0;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 44px;
  color: #003137;
  z-index: 5;
}

.biome__wrapper {
  position: relative;
  background-color: #f3f1d8;
}

.biome__wrapper::before {
  display: block;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  left: 0;
  right: 0;
  z-index: 1;
  background-image: url("../img/biom.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  filter: blur(14px);
  opacity: 0.3;
}

.biome__image {
  display: block;
  width: 328px;
  position: absolute;
  bottom: -85px;
  left: calc(50% - 164px);
  right: 0;
  z-index: 5;
}

.biome__text {
  margin: 0;
  padding: 0 0 57px;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 24px;
  line-height: 140%;
  color: #E1F1E3;
}

.biome__button {
  width: 200px;
  min-height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 12px 36px;
  background: #E0804E;
  border-radius: 64px;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #ffffff;
}

.achievement {
  background-color: #ffffff;
}

.achievement__container {
  padding: 47px 16px 56px;
  background-image: url("../img/aqm_3d_logo_mob.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.achievement__list {
  display: flex;
  flex-direction: column;
  gap: 18px;
  margin: 0;
  padding: 0;
}

.achievement__item-wrapper {
  display: flex;
}

.achievement__item-wrapper:nth-child(even) {
  margin-left: auto;
}

.achievement__amount {
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 90px;
  line-height: 108px;
  color: #254634;
}

.achievement__text {
  padding-top: 17px;
  margin-left: 10px;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 16px;
  color: #254634;
}

.client {
  background-color: #254634;
}

.client__container {
  padding: 82px 16px 106px;
}

.client__title {
  margin: 0;
  padding: 0;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 35px;
  line-height: 69px;
  color: #E1F1E3;
}

.client__text {
  margin: 0 0 82px;
  padding: 0;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #E1F1E3;
}

.client__image-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 30px;
}

.client__image {
  width: 75px;
  height: 75px;
  display: block;
}

.magazine {
  background-color: #ffffff;
}

.magazine__container {
  padding: 72px 16px 147px;
}

.magazine__title {
  margin: 0 0 69px;
  padding: 0;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 35px;
  line-height: 69px;
  color: #151919;
}

.magazine__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.magazine__item {
  min-height: 100px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #5D9F93;
}

.magazine__item:first-child {
  border-top: 1px solid #5D9F93;
}

.magazine__item-name,
.magazine__download {
  display: block;
}

.magazine__item-name {
  width: 120px;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 135%;
  color: #151919;
}

.magazine__link {
  position: relative;
  padding-right: 18px;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #E0804E;
}
.magazine__link::before {
  position: absolute;
  content: "";
  right: 0;
  top: 2px;
  width: 13px;
  height: 15px;
  background-image: url("../img/download_sign.svg");
  background-repeat: no-repeat;
  background-size: 13px 15px;
}

.contacts {
  position: relative;
  overflow: hidden;
  background-color: #003137;
}

.contacts__container {
  padding: 50px 16px 0;
}
.contacts__container::before {
  position: absolute;
  content: "";
  top: 250px;
  left: max(53vw - 295px, 10px);
  width: min(140vw, 700px);
  height: min(140vw, 700px);
  background-image: url("../img/aqm_logo_full.svg");
  background-position: -9px -9px;
  background-size: cover;
  background-repeat: no-repeat;
  z-index: 1;
  transform: rotate(180deg);
}

.contacts__box {
  display: flex;
  justify-content: space-between;
  margin-bottom: 59px;
}

.contacts__title {
  margin: 0 0 10px;
  padding: 0;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 35px;
  line-height: 69px;
  color: #E1F1E3;
}

.address__title {
  margin: 0 0 5px;
  padding: 0;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  align-items: center;
  color: #E1F1E3;
}

.address__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.address__link {
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 135%;
  color: #E1F1E3;
}

.social__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.social__title {
  margin: 0 0 5px;
  padding: 0;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  align-items: center;
  color: #E1F1E3;
}

.social__link {
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 135%;
  color: #E1F1E3;
}

.form {
  position: relative;
  display: flex;
  flex-direction: column;
  gap: 18px;
  padding: 0 0 39px 35px;
  margin: 0 0 0 16px;
  z-index: 5;
}

.form__wrapper {
  z-index: 5;
}

.form__reason {
  margin: 0;
  padding: 0;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  color: #E1F1E3;
}

.form__input {
  width: 100%;
  padding: 0;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 40px;
  color: #E1F1E3;
  background-color: #003137;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid #5D9F93;
}

.form__textarea {
  padding: 0;
  max-height: 420px;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 40px;
  color: #E1F1E3;
  background-color: #003137;
  border-top: none;
  border-left: none;
  border-right: none;
  border-bottom: 1px solid #5D9F93;
  resize: vertical;
}

.form__btn-submit {
  width: 175px;
  padding: 12px 36px;
  background: #E0804E;
  border-radius: 64px;
  border: none;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #ffffff;
  cursor: pointer;
  z-index: 5;
}

.footer {
  background-color: #151919;
}

.footer__container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  position: relative;
  padding: 32px 96px 50px 16px;
}

.footer__copyright {
  position: absolute;
  bottom: 17px;
  left: 16px;
  display: block;
  font-family: "Akkurat LL", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #E1F1E3;
}

.footer__logo {
  width: 100%;
  margin-right: auto;
}

.main-article__box--up {
  position: relative;
  min-height: 417px;
  background-color: #E1F1E3;
}

.main-article__box--down {
  background-color: #ffffff;
}

.main-article__image {
  display: block;
  position: absolute;
}

.main-article__title {
  margin: 0;
  padding: 0;
}

.main-article__text {
  margin: 0;
  padding: 0;
}